import { Api } from "../axios-config/Index";

interface IAuth {
  accessToken: string;
}
const auth = async (
  email: string,
  password: string
): Promise<IAuth | Error> => {
  // o number e para retorna um pessoa  que e um id.
  try {
    const { data } = await Api.get("/auth", { data: { email, password } }); // faz o posto e retorna uma pessoa e sua identificao e um id, so um dado e no todos, como retorna so um id ento e mais rapido a resposta e no demora para pasar todos os dados ('/cidades', dados), na pagina de cidades passa os dados

    if (data) {
      return data;
    }
    return new Error("Erro Login");
  } catch (error) {
    console.error(error);
    return new Error((error as { message: string }).message || "Login");
  }
};

export const AuthService = {
  auth,
};
