
import {
  useCallback,
  useState,
  useContext,
  createContext,
} from "react";

interface IDrawerOptions { // com o drawer optionms navega pra aa aplicao toda
  icons: string;
  label: string;
  path: string; //navergar
}

interface IDrawerContextData {
  isdrawerOpen: boolean;
  drawerOptions: IDrawerOptions[];
  setDrawerOptions: (newDrawerOptions: IDrawerOptions[]) => void;
  toggleDrawerOpen: () => void; // alterna os ttemas, passa no calback como nome funo
}

const DrawerContext = createContext({} as IDrawerContextData);

export const useDrawerContext = () => {
  // o useThemeContext retorna as propriedade dentro do usecontext, e onde alterna o tema, e export para as rotas
  return useContext(DrawerContext); 
};

interface Ichildren {
  children: React.ReactNode;
}


export const DrawerProvider: React.FC<Ichildren> = ({ children }) => {
  const [isdrawerOpen, setDrawerOpen] = useState(false); // menu comea fechado pra android
  const [drawerOptions, setDrawerOptions] = useState<IDrawerOptions[]>([]);// com essa funo do drawer options voc seta o menu

  const toggleDrawerOpen = useCallback( // o mesmo nome da interface
    // armazena funoes dentro //1 e ideal quando vai usar na aplicao inteira e no so em uma parte da aplicao. se for so em um componente pode ser uma funo, como e em uma aplicao inteira ento no precisa esta reiniciando a aplicao. usa mais em contexto ou em componentes.
    () => {
      setDrawerOpen((oldDrawerOpen) => !oldDrawerOpen); // se tiver aberto fecha, se tiver fechado abrir, alterna
    },
    []
  );

  const handleDrawerOptions = useCallback((newDrawerOptions: IDrawerOptions[]) => {
     setDrawerOptions(newDrawerOptions); // essa funo vai para toda a aplicao
    },
    [],
  );

  return (
    <DrawerContext.Provider value={{ isdrawerOpen, drawerOptions, toggleDrawerOpen, setDrawerOptions: handleDrawerOptions}}>
        {children}
       
    </DrawerContext.Provider>
  );
};
