import {
  Button,
  Icon,
  LinearProgress,
  Pagination,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableFooter,
  TableHead,
  TableRow,
} from "@mui/material";
import { useEffect, useMemo, useState } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { FerramentasDaListagem } from "../../shared/components";
import { Enviroment } from "../../shared/environment";
import { useDebounce } from "../../shared/hooks";
import { LayoutePages } from "../../shared/layouts";
import {
  IPessoas,
  PessoasService,
} from "../../shared/services/api/passoas/PessoasService";

export const Pessoas: React.FC = () => {
  const { debounce } = useDebounce(); //
  const [searchParams, setSerchParams] = useSearchParams(); // essa funo e para quando usuari for compartilha algum item ele poder enviar a URL  do item onde estava e no do site
  const busca = useMemo(() => {
    // e para fazer busca e usado so quando e ecionado
    return searchParams.get("busca") || ""; // busca o paramentro de busca e entrega (busca), neavega em busca na pargia
  }, [searchParams]);

  const pagina = useMemo(() => {
    return Number(searchParams.get("pagina") || "1"); // quantidade de paginas. inicia sempre em um
  }, [searchParams]);

  const [rows, setRows] = useState<IPessoas[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [totalR, setTotalR] = useState(0);

  useEffect(() => {
    setIsLoading(true); // se ouver atraso ele vai carregar os dados do back-end
    debounce(() => {
      // o then e quando o service retorna ele evai executar uma funo
      PessoasService.getAll(pagina, busca).then((result) => {
        setIsLoading(false); // quando tiver os dados no precisa carregar os dados novamente, se ja chegou aqui esta ok
        // no resul passa as pessoas ou erro, no getall tem as propriedades de pages e filter, em pagem passa (10 em filter passa (busca)
        if (result instanceof Error) {
          alert(result.message); // em caso de erro
        } else {
          // em caso de sucesso
          console.log(result);
          setTotalR(result.totalPessoas);
          setRows(result.data);
        }
      });
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [busca, pagina]);

  const handleDelete = (id: number) => {
    // eslint-disable-next-line no-restricted-globals
    if (confirm('Realmente quer apagar?')) {
      PessoasService.deleteById(id).then(result => {
        if (result instanceof Error) {
          alert(result.message);
        }else{
          setRows(oldRows => [
          ...oldRows.filter(oldRows => oldRows.id !== id)
          ])
          alert('apagado com sucesso!!!')
        }
      });
    }
  };


  const navigate = useNavigate();
  return (
    <>
      <LayoutePages
        titulo="Pagina de Pessoas"
        barraDeFerramentas={
          <FerramentasDaListagem
            mostrarInputBusca // poderia colocar true, mas o react intende que e true
            textoBotaoVovo="Nova"
            clicarNovo={() => navigate("/pessoas/detalhes/nova")}
            textoBusca={busca}
            mudarTextoBusca={(texto) =>
              setSerchParams({ busca: texto, pagina: "1" }, { replace: true })
            } // o replace >> e para buscar impede que registra varias rotas, e quando voc visita varias paginas e ele volta para o historico
          />
        }>
        <TableContainer
          component={Paper}
          variant="outlined"
          sx={{ marginLeft: 1, width: "auto" }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell width={200}>Aes</TableCell>
                <TableCell>Nome</TableCell>
                <TableCell>Email</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => (
                <TableRow key={row.id}>
                  <TableCell>
                    <Button size="small" onClick={() => handleDelete(row.id)}>
                      <Icon>delete</Icon>
                    </Button>
                    <Button
                      size="small"
                      onClick={() => navigate(`/pessoas/detalhes/${row.id}`)}>
                      <Icon>edite</Icon>
                    </Button>
                  </TableCell>
                  <TableCell>{row.nome}</TableCell>
                  <TableCell>{row.email}</TableCell>
                </TableRow>
              ))}
            </TableBody>
            <TableFooter>
              {isLoading && (
                <TableRow>
                  <TableCell colSpan={3}>
                    <LinearProgress variant="indeterminate" />
                  </TableCell>
                </TableRow>
              )}
              {totalR > 0 && totalR > Enviroment.LIMITE_DE_LINHAS && (
                <TableRow>
                  <TableCell colSpan={3}>
                    <Pagination
                      page={pagina}
                      count={Math.ceil(totalR / Enviroment.LIMITE_DE_LINHAS)} // contas as paginas
                      onChange={(_, newPage) =>
                        setSerchParams(
                          { busca, pagina: newPage.toString() },
                          { replace: true }
                        )
                      } // essa funo e para navegar na paginas clicar onde quizer ex pg7
                    />
                  </TableCell>
                </TableRow>
              )}
            </TableFooter>
          </Table>
        </TableContainer>
      </LayoutePages>
    </>
  );
};

